@import '~antd/dist/antd.css';
.ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected {
  background: rgba(255, 255, 255, 0.05);
}
.ant-menu {
  background: black;
}
.ant-menu-submenu-popup {
  background: black;
}
.ant-menu-vertical {
  background: black;
}
.ant-menu-item {
  padding: 0px;
  margin: 0px;
  width: 100%;
  background: #070707;
}
.MNmenu {
  background: #070707;
}
.MNsubmenu-title-wrapper {
  background: #070707;
}
.MNmenuGroup {
  background: #070707;
}
.MNitem {
  background: #070707;
}
@media only screen and (max-width: 800px) {
  .MNmenu {
    position: relative;
    left: -8px;
  }
}
