@timelineHeight   : 40vh;
@timelineBoxHeight: calc(40vh + 50px);
@borderColor      : rgb(20, 20, 20);

.HQcontainer {
  width             : 100%;
  height            : 100%;
  // height         : calc(100% - 100px);
  display           : flex;
  // justify-content: center;
  justify-content   : flex-start;
  flex-direction    : column;
  // position       : absolute;
  // bottom         : 0px;  
  // background     : @loGray;
}

.HQtopContainer {
  // min-height            : 200px;
  // height                : 100%;
  height                   : calc(100% - @timelineBoxHeight);
  // height                : 500px;
  width                    : 100%;
  display                  : flex;
  flex-wrap                : nowrap;
  flex-shrink              : 0;
  align-items              : center;
  justify-content          : space-evenly;
  // padding               : 15px 45px 15px 55px;
  background-color         : @borderColor;
  border                   : 5px solid @borderColor;
  // border-bottom         : 1px solid fade(white, 10%);
}

.HQconsole {
  width          : 100%;
  // max-width   : calc(100vw - 800px);
  min-width      : 400px;
  height         : 100%;
  border-right   : 5px solid @borderColor;
}

.HQuiControls {
  width                    : 40%;
  max-width                : 800px;
  height                   : 100%;
  padding                  : 10px 0px 10px 10px;
  border-radius            : 5px;
  background               : shade(@layout-body-background, 60%);
  // border                : 1px solid fade(white, 5%);
  // background            : fade(@border-color-split, 20%);
  // background            : pink;
  display                  : flex;
  flex-wrap                : nowrap;
  flex-shrink              : 0;
  justify-content          : center;
  align-items              : center;
}

.HQtimelineBox {
  width              : 100%;
  padding            : 5px 20px 10px 5px;
  display            : flex;
  flex-wrap          : nowrap;
  flex-direction     : column;
  flex-shrink        : 0;
  justify-content    : center;
  // align-items     :   flex-end;
  height             : @timelineHeight;
  position           : absolute;
  bottom             : 0px;
  // border          : 5px solid @borderColor;
  // border-top      : 1px solid fade(white, 10%);
  // background      : pink;
  // margin-top      : 10px;
  // background      : fade(white, 3%);
}

.HQtimelineButtonRow {
  padding     : 5px 20px 5px 55px;
  // position : relative;
  // top      : 2px;
  flex-wrap   : nowrap;
}

.HQtimelineButtonUnit {
  padding            : 0px 12px 0px 12px;
  // background-color: yellow;
}

.HQbuttonText {
  color    : @midGray;
  font-size: 1.2em;
  /*left   : -15px;*/
}


@media only screen and (max-width: 800px) {
  .HQcontainer {
    position       : static;
    justify-content: flex-start;
  }

  .HQtopContainer {
    justify-content       : center;
    // background-color   : fade(black, 50%);
    border-top            : 0px solid @borderColor;
    border-bottom         : 0px solid @borderColor;
    border-right          : 0px solid @borderColor;
    border-left           : 0px solid @borderColor;
  }

  .HQuiControls {
    padding: 5px 0px 18px 10px;
    width  : 100vw;
    border-radius: 0px;
  }

  .HQtimelineButtonRow {
    padding: 0px 5px 5px 10px;
  }

  .HQtimelineButtonUnit {
    padding: 0px 0px 0px 10px;
  }

  .HQbuttonText {
    color    : @hiGray;
    font-size: 1em;
    /*left   : -15px;*/
  }

}

@media only screen and (max-height: 500px) {
  .HQtimelineBox {
    height    : calc(100% - 50px);
    border-top: 0px;
  }
}

